import React from 'react'
import { NavLink } from 'react-router-dom';

const Login = () => {
    return (
        <>
            <section>
                <div class="container">
                    <div class="row">
                        <div class="col-md-4 mx-auto " style={{ marginTop: '5%' }}>
                            <div className="card login-form">
                                <div className="login_card_body card-body">
                                    <NavLink exact to="#" className="navbar-brand"><img src='https://logodix.com/logo/336725.png' alt="logo" className="logo" /></NavLink>
                                    <div className="card-text">
                                        <form>
                                            <div className="form-group text-left">
                                                <label htmlFor="exampleInputEmail1">Email address</label>
                                                <input type="email" className="form-control form-control-md mx-auto" placeholder="email" id="exampleInputEmail1" aria-describedby="emailHelp" />
                                            </div>
                                            <div className="form-group text-left">
                                                <label htmlFor="exampleInputPassword1">Password</label>
                                                <input type="password" className="form-control form-control-md mx-auto" placeholder="password" id="exampleInputPassword1" />
                                                <NavLink to="/forgot_password" class="my-2" style={{ float: 'right', fontSize: 12 }}>Forgot password?</NavLink>
                                            </div>
                                            <NavLink exact to="/"> <button type="submit" className="btn btn-primary btn-block mx-auto my-2">Log in</button></NavLink>
                                            <div className="sign-up my-2">or login with

                                                <div class="footer-social-icon my-2">
                                                    <a href="#"><i class="fab fa-facebook-f facebook-bg"></i></a>
                                                    <a href="#"><i class="fab fa-twitter twitter-bg"></i></a>
                                                    <a href="#"><i class="fab fa-google-plus-g google-bg"></i></a>
                                                </div>
                                            </div>
                                            <div className="sign-up my-2">New to Page To Image? <NavLink exact to="/sign_up">Create an account.</NavLink>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Login